<template>
  <div>
    <h2>Responsive Form</h2>

    <div class="container">
      <div class="row" v-for="(item, index) in objset.fields" v-bind:key="index">
        <div v-if="item.type === 'text' || item.type === 'password' ">
          <div class="col-25">
            <label for="fname">{{index}}</label>
          </div>
          <div class="col-75">
            <input :type="item.type" :placeholder="item.value" v-model="item.result"/>
          </div>
        </div>


        <div v-if="item.type === 'select' ">
          <div class="col-25">
            <label for="fname">{{index}}</label>
          </div>
          <div class="col-75">
            <select v-model="item.result">
              <option disabled value="">Please select one</option>
              <option v-for="data in item.value" v-bind:key="data" :value="data">{{data}}</option>
            </select>
          </div>
        </div>


        <div v-if="item.type === 'button' ">
          <div class="col-75">
            <input :type="item.type"  name="firstname" :value="item.value" @click="callback"/>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      objset: []
    };
  },
  props: ["dataset"],
  methods: {
    callback() {
      let data = [];
      for(let i in this.objset.fields) {
        console.log(i)
        if (this.objset.fields[i].result) {
          data[i] = this.objset.fields[i].result
        }
      }
      this.$emit("mobiotics-form", data);
    }
  },
  mounted() {
    console.log("dataset", this.dataset);
    this.objset = this.dataset;
  }
};
</script>

