  padding: 12px 12px 12px 0;
  display: inline-block;
}

input[type="button"] {
     background-color: #4caf50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin: 21px auto;
    display: block;
    width: 100%;
    font-size: 19px;

}

input[type="button"]:hover {
  background-color: #45a049;
}

.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.col-25 {
  float: left;
